/* src/app/components/todo-list/todo-list.component.css */
.todo-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}
h1 {
  text-align: center;
  color: #333;
  margin-bottom: 30px;
  font-family: ui-sans-serif;
}
.add-todo {
  display: flex;
  margin-bottom: 20px;
  gap: 10px;
}
.todo-input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
}
.add-btn {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}
.add-btn:hover:not(:disabled) {
  background-color: #0056b3;
}
.add-btn:disabled {
  cursor: not-allowed;
}
.loading {
  text-align: center;
  padding: 20px;
  color: #666;
}
.empty-message {
  text-align: center;
  color: #666;
  font-style: italic;
  padding: 20px;
}
.todo-item {
  display: flex;
  align-items: center;
  padding: 12px;
  border: 1px solid #eee;
  border-radius: 4px;
  margin-bottom: 10px;
  background-color: #fff;
  gap: 12px;
}
.todo-item.completed {
  background-color: #f8f9fa;
}
.todo-checkbox {
  width: 18px;
  height: 18px;
}
.todo-title {
  flex: 1;
  font-size: 16px;
}
.todo-item.completed {
  color: #fff;
  background-color: rgb(49, 155, 119);
}
.todo-item.completed .todo-title {
  text-decoration: line-through;
}
.delete-btn {
  padding: 6px 12px;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}
.delete-btn:hover {
  background-color: #c82333;
}
.edit-input {
  flex: 1;
  padding: 8px;
  border: 1px solid #007bff;
  border-radius: 4px;
  font-size: 16px;
  outline: none;
}
.todo-actions {
  display: flex;
  gap: 8px;
}
.edit-btn {
  padding: 6px 12px;
  background-color: rgb(49, 155, 119);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}
.edit-btn:hover:not(:disabled) {
  background-color: #218838;
}
.edit-btn:disabled {
  background-color: #ccc;
  cursor: not-allowed;
  opacity: 0.6;
}
.save-btn {
  padding: 6px 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}
.save-btn:hover:not(:disabled) {
  background-color: #0056b3;
}
.save-btn:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}
.cancel-btn {
  padding: 6px 12px;
  background-color: #6c757d;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}
.cancel-btn:hover {
  background-color: #5a6268;
}
/*# sourceMappingURL=todo-list.component.css.map */
